"use client";
import { zodResolver } from "@hookform/resolvers/zod";
import { useToast } from "@/hooks/use-toast";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { CalendarIcon, Image as ImageIcon, MapPin, Users, Ticket, Tag, List } from "lucide-react";
import {
    Form,
    FormControl,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
    FormDescription,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Calendar } from "@/components/ui/calendar";
import { Checkbox} from "@/components/ui/checkbox";
import { MultiSelect } from "@/components/ui/multi-select";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { format } from "date-fns";
import { cn } from "@/lib/utils";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useDropzone } from "react-dropzone";
import { useState } from "react";

const eventSchema = z.object({
    title: z.string().min(3, "Title must contain at least 3 characters"),
    description: z.string().min(10, "Description must contain at least 10 characters"),
    longDescription: z.string().optional(), // إضافة وصف طويل
    eventType: z.enum(["academic", "social", "workshop"]),
    startDate: z.date(),
    endDate: z.date(),
    time: z.number().min(1, "Time must be at least 1 hour"),
    location: z.string().min(3, "Location is required"),
    hall: z.string().optional(), // إضافة القاعة
    capacity: z.number().min(1, "Capacity must be at least 1"),
    department: z.string().min(2, "Department is required"),
    status: z.enum(["pending", "approved", "rejected"]).optional(),
    price: z.number().optional(),
    categories: z.string().min(1, "Categories is required"),
    features: z.array(z.string()).optional(), // إضافة المميزات كقائمة
    isSpecial: z.boolean().optional(), // إضافة حقل الحدث المميز
    image: z.instanceof(File)
});


const eventCategories = [
    "Academic Seminars",
    "Workshops",
    "Exhibitions",
    "Entertainment Events",
    "Competitions",
    "Conferences"
];
// API_URL=http://localhost:3001     الموجود في ملف ال  .evn.local
const CreateEventPage = () => {


    const { toast } = useToast();
    const [loading, setLoading] = useState(false);

    const form = useForm<z.infer<typeof eventSchema>>({
        resolver: zodResolver(eventSchema),
        defaultValues: {
            title: "",
            description: "",
            longDescription: "",
            capacity: 50,
            categories: "",
            hall: "",
            features: [],
            isSpecial: false,
            time: 1, // قيمة افتراضية للوقت
        },
    });

    const { getRootProps, getInputProps, isDragActive } = useDropzone({
        accept: {
            'image/*': ['.png', '.jpg', '.jpeg']
        },
        maxFiles: 1,
        onDrop: (acceptedFiles) => {
            form.setValue("image", acceptedFiles[0]);
        }
    });
    const rootProps = getRootProps();

    const onSubmit = async (values: z.infer<typeof eventSchema>) => {
        try {
            setLoading(true);

            const formData = new FormData();
            formData.append("title", values.title);
            formData.append("description", values.description);
            formData.append("longDescription", values.longDescription || "");
            formData.append("eventType", values.eventType);
            formData.append("startDate", values.startDate.toISOString());
            formData.append("endDate", values.endDate.toISOString());
            formData.append("location", values.location);
            formData.append("hall", values.hall || "");
            formData.append("capacity", values.capacity.toString());
            formData.append("time", values.time.toString());
            formData.append("department", values.department);
            formData.append("isSpecial", values.isSpecial ? "true" : "false");

            if (values.price) {
                formData.append("price", values.price.toString());
            }

            if (values.features) {
                values.features.forEach((feature, index) => {
                    formData.append(`features[${ index }]`, feature);
                });
            }

            formData.append("image", values.image);

            const response = await fetch(`${process.env.API_URL}/events`, {
                method: "POST",
                credentials: "include",
                body: formData,
            });

            // باقي الكود كما هو...
        } catch (err) {
            // معالجة الأخطاء
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-gray-900 to-blue-900 relative overflow-hidden p-4">
            {/* Animated background elements */}
            <motion.div
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 0.2, scale: 1 }}
                transition={{ duration: 2, ease: "easeOut" }}
                className="absolute top-0 left-0 w-full h-full bg-[url('/grid-pattern.svg')] bg-repeat opacity-20"
            />

            <motion.div
                initial={{ x: -100, y: -100 }}
                animate={{ x: 0, y: 0 }}
                transition={{ duration: 1.5 }}
                className="absolute w-64 h-64 bg-blue-600/20 rounded-full -top-32 -left-32 blur-xl"
            />

            <motion.div
                initial={{ x: 100, y: 100 }}
                animate={{ x: 0, y: 0 }}
                transition={{ duration: 1.5, delay: 0.2 }}
                className="absolute w-72 h-72 bg-purple-600/20 rounded-full -bottom-32 -right-32 blur-xl"
            />

            <AnimatePresence>
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                    className="flex items-center justify-center min-h-screen"
                >
                    <motion.div
                        whileHover={{ scale: 1.005 }}
                        className="w-full max-w-2xl bg-white/5 backdrop-blur-lg rounded-2xl border border-white/10 shadow-2xl overflow-hidden"
                    >
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            transition={{ delay: 0.2 }}
                            className="p-8"
                        >
                            <motion.div
                                initial={{ y: -20 }}
                                animate={{ y: 0 }}
                                className="flex flex-col items-center mb-8"
                            >
                                <motion.div
                                    whileHover={{ rotate: 5, scale: 1.1 }}
                                    className="p-4 bg-gradient-to-r from-blue-600 to-purple-600 rounded-full mb-4"
                                >
                                    <CalendarIcon className="h-8 w-8 text-white" />
                                </motion.div>
                                <motion.h1
                                    className="text-3xl font-bold text-white mb-2 text-center"
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.3 }}
                                >
                                    Create New Event
                                </motion.h1>
                                <p className="text-white/80 text-center">
                                    Fill in the details to organize your amazing event
                                </p>
                            </motion.div>

                            <Form {...form}>
                                <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                                    {/* Title & Description */}
                                    <div className="grid md:grid-cols-2 gap-6">
                                        {['title', 'description'].map((field, index) => (
                                            <motion.div
                                                key={field}
                                                initial={{ opacity: 0, y: 20 }}
                                                animate={{ opacity: 1, y: 0 }}
                                                transition={{ delay: 0.5 + index * 0.1 }}
                                            >
                                                <FormField
                                                    control={form.control}
                                                    name={field as keyof typeof form.watch}
                                                    render={({ field: formField }) => (
                                                        <FormItem>
                                                            <FormLabel className="text-white/80">
                                                                {field === 'title' ? 'Event Title' : 'Description'}
                                                            </FormLabel>
                                                            <FormControl>
                                                                {field === 'title' ? (
                                                                    <Input
                                                                        placeholder="Enter event title"
                                                                        {...formField}
                                                                        className="bg-white/5 border-white/20 text-white placeholder-white/50"
                                                                    />
                                                                ) : (
                                                                    <Textarea
                                                                        placeholder="Describe your event"
                                                                        {...formField}
                                                                        className="bg-white/5 border-white/20 text-white placeholder-white/50 min-h-[120px]"
                                                                    />
                                                                )}
                                                            </FormControl>
                                                            <FormMessage className="text-red-400" />
                                                        </FormItem>
                                                    )}
                                                />
                                            </motion.div>
                                        ))}
                                    </div>

                                    {/* Dates Section */}
                                    <motion.div
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        transition={{ delay: 0.7 }}
                                        className="grid md:grid-cols-2 gap-6"
                                    >
                                        {['startDate', 'endDate'].map((dateField, index) => (
                                            <FormField
                                                key={dateField}
                                                control={form.control}
                                                name={dateField as 'startDate' | 'endDate'}
                                                render={({ field }) => (
                                                    <FormItem className="flex flex-col">
                                                        <FormLabel className="text-white/80">
                                                            {dateField === 'startDate' ? 'Start Date' : 'End Date'}
                                                        </FormLabel>
                                                        <Popover>
                                                            <PopoverTrigger asChild>
                                                                <FormControl>
                                                                    <motion.div whileHover={{ scale: 1.01 }}>
                                                                        <Button
                                                                            variant="outline"
                                                                            className={cn(
                                                                                "pl-3 text-left font-normal bg-white/5 border-white/20 hover:bg-white/10",
                                                                                !field.value && "text-white/50"
                                                                            )}
                                                                        >
                                                                            {field.value ? (
                                                                                format(field.value, "PPP")
                                                                            ) : (
                                                                                <span>Pick a date</span>
                                                                            )}
                                                                            <CalendarIcon className="ml-auto h-4 w-4 opacity-70" />
                                                                        </Button>
                                                                    </motion.div>
                                                                </FormControl>
                                                            </PopoverTrigger>
                                                            <PopoverContent className="w-auto p-0 bg-gray-800 border-gray-700">
                                                                <Calendar
                                                                    mode="single"
                                                                    selected={field.value}
                                                                    onSelect={field.onChange}
                                                                    initialFocus
                                                                    className="bg-gray-800"
                                                                    classNames={{
                                                                        day_selected: "bg-gradient-to-r from-blue-600 to-purple-600",
                                                                    }}
                                                                />
                                                            </PopoverContent>
                                                        </Popover>
                                                        <FormMessage className="text-red-400" />
                                                    </FormItem>
                                                )}
                                            />
                                        ))}
                                    </motion.div>

                                    {/* Location & Capacity */}
                                    <motion.div
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        transition={{ delay: 0.8 }}
                                        className="grid md:grid-cols-2 gap-6"
                                    >
                                        {['location', 'capacity'].map((field, index) => (
                                            <FormField
                                                key={field}
                                                control={form.control}
                                                name={field as 'location' | 'capacity'}
                                                render={({ field: formField }) => (
                                                    <FormItem>
                                                        <FormLabel className="text-white/80">
                                                            {field === 'location' ? 'Location' : 'Capacity'}
                                                        </FormLabel>
                                                        <FormControl>
                                                            <div className="relative">
                                                                <Input
                                                                    type={field === 'capacity' ? 'number' : 'text'}
                                                                    placeholder={
                                                                        field === 'location'
                                                                            ? 'Enter event location'
                                                                            : 'Enter max attendees'
                                                                    }
                                                                    {...formField}
                                                                    className="bg-white/5 border-white/20 text-white placeholder-white/50 pl-10"
                                                                />
                                                                {field === 'location' ? (
                                                                    <MapPin className="absolute left-3 top-3 h-4 w-4 text-white/50" />
                                                                ) : (
                                                                    <Users className="absolute left-3 top-3 h-4 w-4 text-white/50" />
                                                                )}
                                                            </div>
                                                        </FormControl>
                                                        <FormMessage className="text-red-400" />
                                                    </FormItem>
                                                )}
                                            />
                                        ))}
                                    </motion.div>

                                    {/* Time & Department */}
                                    <motion.div
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        transition={{ delay: 0.6 }}
                                        className="grid md:grid-cols-2 gap-6">

                                        <FormField control={form.control} name="time"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">
                                                        Duration (in hours)
                                                    </FormLabel>
                                                    <FormControl>
                                                        <Input type="number" placeholder="e.g., 2" {...field}
                                                            className="bg-white/5 border-white/20 text-white placeholder-white/50"
                                                        />
                                                    </FormControl>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )} />

                                        <FormField control={form.control} name="department"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">
                                                        Organizing Department
                                                    </FormLabel>
                                                    <FormControl>
                                                        <Input type="text" placeholder="e.g., CSE Department" {...field}
                                                            className="bg-white/5 border-white/20 text-white placeholder-white/50"
                                                        />
                                                    </FormControl>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )} />
                                    </motion.div>

                                    {/* Hall & Time */}
                                    <div className="grid md:grid-cols-2 gap-6">
                                        <FormField
                                            control={form.control}
                                            name="hall"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">Hall (Optional)</FormLabel>
                                                    <FormControl>
                                                        <Input
                                                            placeholder="Enter hall name"
                                                            {...field}
                                                            className="bg-white/5 border-white/20 text-white placeholder-white/50"
                                                        />
                                                    </FormControl>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )}
                                        />

                                        <FormField
                                            control={form.control}
                                            name="time"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">Duration (hours)</FormLabel>
                                                    <FormControl>
                                                        <Input
                                                            type="number"
                                                            placeholder="Event duration in hours"
                                                            {...field}
                                                            onChange={(e) => field.onChange(parseInt(e.target.value))}
                                                            className="bg-white/5 border-white/20 text-white placeholder-white/50"
                                                        />
                                                    </FormControl>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )}
                                        />
                                    </div>

                                    {/* Long Description */}
                                    <FormField
                                        control={form.control}
                                        name="longDescription"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-white/80">Long Description (Optional)</FormLabel>
                                                <FormControl>
                                                    <Textarea
                                                        placeholder="Detailed description of your event"
                                                        {...field}
                                                        className="bg-white/5 border-white/20 text-white placeholder-white/50 min-h-[120px]"
                                                    />
                                                </FormControl>
                                                <FormMessage className="text-red-400" />
                                            </FormItem>
                                        )}
                                    />

                                    {/* Features */}
                                    <FormField
                                        control={form.control}
                                        name="features"
                                        render={({ field }) => (
                                            <FormItem>
                                                <FormLabel className="text-white/80">Event Features (Optional)</FormLabel>
                                                <FormControl>
                                                    <MultiSelect
                                                        options={[
                                                            "Live Demos",
                                                            "Tech Talks",
                                                            "Networking",
                                                            "Food & Drinks",
                                                            "Workshops",
                                                            "Competitions"
                                                        ]}
                                                        selected={field.value || []}
                                                        onChange={(selected) => field.onChange(selected)}
                                                        placeholder="Select features..."
                                                    />
                                                </FormControl>
                                                <FormMessage className="text-red-400" />
                                            </FormItem>
                                        )}
                                    />

                                    {/* Is Special */}
                                    <FormField
                                        control={form.control}
                                        name="isSpecial"
                                        render={({ field }) => (
                                            <FormItem className="flex flex-row items-center space-x-3 space-y-0">
                                                <FormControl>
                                                    <Checkbox
                                                        checked={field.value}
                                                        onCheckedChange={field.onChange}
                                                        className="border-white/30 data-[state=checked]:bg-blue-600"
                                                    />
                                                </FormControl>
                                                <div className="space-y-1 leading-none">
                                                    <FormLabel className="text-white/80 cursor-pointer">
                                                        Mark as Special Event
                                                    </FormLabel>
                                                    <FormDescription className="text-white/60">
                                                        Special events will be highlighted on the homepage
                                                    </FormDescription>
                                                </div>
                                            </FormItem>
                                        )}
                                    />

                                    {/* Price & Categories */}
                                    <motion.div
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        transition={{ delay: 0.9 }}
                                        className="grid md:grid-cols-2 gap-6"
                                    >
                                        <FormField
                                            control={form.control}
                                            name="price"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">Ticket Price (Optional)</FormLabel>
                                                    <FormControl>
                                                        <div className="relative">
                                                            <Input
                                                                type="number"
                                                                placeholder="Enter ticket price"
                                                                {...field}
                                                                className="bg-white/5 border-white/20 text-white placeholder-white/50 pl-10"
                                                            />
                                                            <Tag className="absolute left-3 top-3 h-4 w-4 text-white/50" />
                                                        </div>
                                                    </FormControl>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )}
                                        />

                                        <FormField
                                            control={form.control}
                                            name="categories"
                                            render={({ field }) => (
                                                <FormItem>
                                                    <FormLabel className="text-white/80">Categories</FormLabel>
                                                    <Select onValueChange={field.onChange} defaultValue={field.value}>
                                                        <FormControl>
                                                            <motion.div whileHover={{ scale: 1.01 }}>
                                                                <SelectTrigger className="bg-white/5 border-white/20 text-white placeholder-white/50">
                                                                    <SelectValue placeholder="Select categories" />
                                                                </SelectTrigger>
                                                            </motion.div>
                                                        </FormControl>
                                                        <SelectContent className="bg-gray-800 border-gray-700">
                                                            {eventCategories.map((category) => (
                                                                <SelectItem
                                                                    key={category}
                                                                    value={category}
                                                                    className="hover:bg-gray-700 focus:bg-gray-700"
                                                                >
                                                                    <div className="flex items-center gap-2">
                                                                        <List className="h-4 w-4" />
                                                                        {category}
                                                                    </div>
                                                                </SelectItem>
                                                            ))}
                                                        </SelectContent>
                                                    </Select>
                                                    <FormMessage className="text-red-400" />
                                                </FormItem>
                                            )}
                                        />
                                    </motion.div>

                                    {/* Event Image */}
                                    <motion.div
                                        initial={{ opacity: 0 }}
                                        animate={{ opacity: 1 }}
                                        transition={{ delay: 0.4 }}
                                    >
                                        <FormLabel className="text-white/80">Event Image</FormLabel>
                                        <div {...rootProps}>
                                            <motion.div
                                                whileHover={{ scale: 1.01 }}
                                                className={`border-2 border-dashed rounded-xl p-8 text-center cursor-pointer transition-all ${isDragActive ? 'border-blue-400 bg-blue-900/20' : 'border-white/20 hover:border-white/40'
                                                    }`}
                                            >
                                                <input {...getInputProps()} />
                                                <ImageIcon className={`mx-auto h-12 w-12 mb-4 ${isDragActive ? 'text-blue-400' : 'text-white/50'
                                                    }`} />
                                                <p className={`${isDragActive ? 'text-blue-300' : 'text-white/70'
                                                    }`}>
                                                    {isDragActive ? 'Drop the image here' : 'Drag & drop an image or click to select'}
                                                </p>
                                                <p className="text-sm text-white/50 mt-2">PNG, JPG (Max 5MB)</p>
                                            </motion.div>
                                        </div>
                                        <FormMessage className="text-red-400" />
                                    </motion.div>

                                    {/* Submit Button */}
                                    <motion.div
                                        initial={{ opacity: 0, y: 20 }}
                                        animate={{ opacity: 1, y: 0 }}
                                        transition={{ delay: 1 }}
                                        className="pt-4"
                                    >
                                        <motion.button
                                            type="submit"
                                            whileHover={{ scale: 1.02 }}
                                            whileTap={{ scale: 0.98 }}
                                            className="w-full py-3 bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white font-medium rounded-lg shadow-lg transition-all"
                                        >
                                            Create Event
                                        </motion.button>
                                    </motion.div>

                                </form>
                            </Form>
                        </motion.div>
                    </motion.div>
                </motion.div >
            </AnimatePresence >
        </div >
    );
};

export default CreateEventPage;